import React from "react";
import PropTypes from "prop-types";
import { Container, Row, Nav, NavItem, NavLink } from "shards-react";
import { Link } from "react-router-dom";
import { CHAIN_ID, CONTRACT_ADDRESSES } from "../../world/constants";

const MainFooter = ({contained, menuItems, copyright}) => (
  <footer className="main-footer d-flex p-2 px-3 bg-white border-top">
    <Container fluid={contained}>
      <Row>
        <Nav>
          {menuItems.map((item, idx) => (
            <NavItem key={idx}>
              <NavLink
                // tag={Link}
                href={item.to}
              >
                {item.title}
              </NavLink>
            </NavItem>
          ))}
        </Nav>
        <span className="copyright ml-auto my-auto mr-2">{copyright}</span>
      </Row>
    </Container>
  </footer>
);

MainFooter.propTypes = {
  /**
   * Whether the content is contained, or not.
   */
  contained: PropTypes.bool,
  /**
   * The menu items array.
   */
  menuItems: PropTypes.array,
  /**
   * The copyright info.
   */
  copyright: PropTypes.string
};

MainFooter.defaultProps = {
  contained: false,
  copyright: "Copyright  2021 WORLD Token",
  menuItems: [
    {
      title: `Token: ${CONTRACT_ADDRESSES.worldToken[CHAIN_ID]}`,
      to: `https://etherscan.io/token/${CONTRACT_ADDRESSES.worldToken[CHAIN_ID]}`
    },
  ]
};

export default MainFooter;
