import { useEffect, useState } from 'react';
import dayjs from 'dayjs';
import { groupDataByKey } from '../world/utils';

let pastStats = {};
let stats = {};
const fetchStats = async (fetcher, cb) => {
  const date = dayjs()
    .millisecond(0)
    .second(0)
    .minute(0)
    .hour(0);
  const pastWeekMonday = date.day(-6);
  const monday = date.day(1);

  const data = await fetcher(pastWeekMonday.unix());
  groupDataByKey(data, stats, pastStats, monday);

  if (cb) {
    cb();
  }
};

const useStats = (fetcher) => {
  const [dataset, setDataset] = useState({
    pastStats,
    stats
  });

  useEffect(() => {
    fetchStats(fetcher, () => {
      setDataset({
        pastStats,
        stats
      });
    });
  }, []);

  return dataset;
};

export default useStats;
